.section_login{
    background-image: url(../../img/team-bg.jpg);
    background-size: cover; /* Чтобы изображение заполнило фон секции */
    height: 750px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 200px;
}

.title_login{
    color: #27557a;
}

.title_form_login {
    background-color: black;
}

.title_form_login input{
    width: 200px;
    height: 25px;
    border: 3px double;
    border-color:  #3f98f1;

}

.title_form_login p{
    font-size: large;
    font-weight: 700;
    color: #27557a;

}

.btn_login{
    cursor: pointer;
    background-color: #3f98f1;
    color: #ffff;
    box-shadow: 0px 1px 6px rgba(46, 47, 66, 0.08), 0px 1px 1px rgba(46, 47, 66, 0.16), 0px 2px 1px rgba(46, 47, 66, 0.08);
    border-radius: 5px;
    border-color: transparent;
    font-family: inherit;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.88;
    text-align: center;
    letter-spacing: 0.04em;
    /* padding: 16px 32px; */
    margin-top: 40px;
    width: 210px;
    /* height: 70px; */
}

.btn_login:hover,
.btn_login:focus{
    background: #466b89;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}